import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import {
  Segment,
} from 'semantic-ui-react';
import fetch from 'isomorphic-fetch';
import dayjs from 'dayjs';

import fadeIn from '../../anime/fadeIn';
import BarChart from '../../components/BarChart';

const Wrapper = styled.div`
  display: flex;
  animation: ${fadeIn} 1s ease;
`;

const InnerWrapper = styled.div`
  margin: 0.25rem 1rem;
`;

const Dashboard = ({
  user,
}) => {
  const [loading, setLoading] = useState(false);
  const [shifts, setShifts] = useState(null);
  const [fulfillRate, setFulfillRate] = useState(null);

  const fetchShifts = async () => {
    // window.dayjs = dayjs;
    // setLoading(true);
    const URL = 'http://dashboard-analytics-dev.6b9bvmucdp.us-east-1.elasticbeanstalk.com/analytics/shifts?endDate=2018-10-5&timeUnit=YEAR';
    
    try {
      const get = await fetch(URL, {
        headers: {
          // 'Session-Token': JSON.parse(localStorage.sessionToken),
          'Content-Type': 'application/json',
        }
      });

      const result = await get.json();
      
      console.log(result);
      
      setShifts(result);
      // setLoading(false);
      // const pendingShifts = result.filter(shift => shift.status === 0);
      // const shiftsToday = dayjs().

      // console.log(pendingShifts)
    } catch (error) {
      console.log(error);
    }
  };


  const fetchFulfillRate = async () => {
    setLoading(true);

    const URL = 'http://dashboard-analytics-dev.6b9bvmucdp.us-east-1.elasticbeanstalk.com/analytics/fulfillment_rate?timeUnit=YEAR&endDate=2019-1-5';
    
    try {
      const get = await fetch(URL, {
        headers: {
          'Content-Type': 'application/json',
        }
      })

      const result = await get.json();
      
      setFulfillRate(result);
      setLoading(false);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    fetchShifts();
    fetchFulfillRate();
  }, [])

  return (
    <Wrapper>
      <Segment loading={loading}>
        {/* <BarChart /> */}
        {JSON.stringify(shifts)}
        <br />
        <br />
        {JSON.stringify(fulfillRate)}
      </Segment>
    </Wrapper>
  );
};

export default Dashboard;
