import React, { Component } from 'react';
import {
  Container,
  Form,
  Header,
  Segment,
  Divider,
  Message,
} from 'semantic-ui-react';
import styled from 'styled-components';
import { Link, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { toast } from 'react-toastify';
import fetch from 'isomorphic-fetch';
import { connect } from 'react-redux';

import fadeIn from '../../../anime/fadeIn';
import { API_URL, CAPTURE_USER } from '../../../constants';

const Wrapper = styled.div`
  height: 100%;
  padding: 2rem 0;
  background-color: #eee;
  animation: ${fadeIn} 1s ease;
`;

class Login extends Component {
  state = {
    loading: false,
    email: '',
    password: '',
    errorMsg: null,
  }

  setValue = ({ name, value }) => this.setState(prevState => ({
    ...prevState,
    [name]: value,
  }));

  handleSubmit = () => {
    const { history, captureUser } = this.props;
    const {
      email,
      password,
    } = this.state;
    this.setState(prevState => ({
      ...prevState,
      loading: true,
    }), async () => {
      console.log('submitting', `${API_URL}/b2b/sign_in`);
      try {
        console.log('trying');
        const login = await fetch(`${API_URL}/b2b/sign_in`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            session: { device_platform: 'web' },
            user: {
              email,
              password,
              provider: 'email',
            }
          })
        });

        const result = await login.json();
        if (result.type === 'NotFound') {
          this.setState(prevState => ({
            ...prevState,
            loading: false,
            errorMsg: JSON.stringify(result.message),
          }));
        } else if (result.object) {
          const { object } = result;
          // console.log(object)
          captureUser(object);
          toast.success(`Signed in ${email}`);
          history.push('/dashboard');
        }
      } catch (error) {
        console.log(error);
        this.setState(prevState => ({
          ...prevState,
          loading: false,
          errorMsg: JSON.stringify(error),
        }));
      }
    });
  }

  render() {
    const {
      loading,
      // email,
      // password,
      errorMsg,
    } = this.state;

    return (
      <Wrapper>
        <Container>
          <Header as="h1">
            Sign In
          </Header>
          <Segment
            color="blue"
          >
            {
              errorMsg
                ? (
                  <Message error>
                    {errorMsg}
                  </Message>
                )
                : null
            }
            <Form>
              <Form.Input
                label="Email"
                type="email"
                name="email"
                onChange={(event, data) => this.setValue(data)}
              />

              <Form.Input
                label="Password"
                type="password"
                name="password"
                onChange={(event, data) => this.setValue(data)}
              />

              <Divider />

              <Form.Button
                onClick={this.handleSubmit}
                primary
                loading={loading}
              >
                Submit
              </Form.Button>
            </Form>
          </Segment>
        </Container>
      </Wrapper>
    );
  }
}

Login.propTypes = {
  history: PropTypes.shape().isRequired,
};

export default connect(
  null,
  dispatch => ({
    captureUser: payload => dispatch({
      type: CAPTURE_USER,
      payload,
    }),
  })
)(withRouter(Login));
