import {
  CAPTURE_USER,
  CLEAR_USER,
} from '../../constants';

function userReducer(state = {
  user: {},
}, action) {
  switch (action.type) {
    case CAPTURE_USER:
      return {
        ...action.payload,
      };
    case CLEAR_USER:
      return {
        user: {},
      };
    default:
      return state;
  }
}

export default userReducer;
